import React, { useState, useEffect } from 'react';
import Utils from './../Utils';
import axios from 'axios';
import _ from 'lodash';

const Trainersales = (props) => {

  const [list, setList] = useState({ loading: true, error: false, pageInfo: {}, data: [] });
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();
  const [filters, setFilters] = useState({ where: { startDate: startDate, endDate: endDate }, limit: 15, start: 0 });
  const [searchtrainer, setSearchTrainer] = useState("");
  const [searchcourse, setSearchCourse] = useState("");

  const fetchList = () => {
    setList({ ...list, loading: true });

    let params = `?limit=${filters.limit}&start=${filters.start}&`;
    params += _.map(filters.where, (v, k) => `where[${k}]=${v}`).join('&');
    axios.get(Utils.apiUrl('sales/trainerlist'+params), Utils.apiHeaders())
      .then(res => {
        if (res.data.success) {
          setList({ ...list, loading: false, error: false, pageInfo: res.data.pageInfo, data: res.data.data });
        } else {
          setList({ ...list, loading: false, error: res.data.message, pageInfo: {}, data: [] });
        }
      })
  };

  useEffect(fetchList, [filters]);

  const gotoPage = (page) => (e) => {
    const start = (page - 1) * filters.limit;
    setFilters({ ...filters, start: start });
  };

  const currencyFormat = (num) => {
    return '$' + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
  }

  const renderRow = (rec, idx) => {
    return <tr key={rec.id}>
      <td>{rec.trainer}</td>
      <td>{rec.name}</td>
      <td>{rec.earning}</td>
    </tr>;
  };

  const handleSearchTrainer = (e) => {
    setSearchTrainer(e.target.value);
  };

  const handleSearchCourse = (e) => {
    setSearchCourse(e.target.value);
  };

  const handleStartDate = (e) => {
    setStartDate(e.target.value);
  };

  const handleEndDate = (e) => {
    setEndDate(e.target.value);
    console.log(startDate);  
    console.log(e.target.value);
    var endDates = e.target.value;
    setFilters({ ...filters, where: { startDate: startDate, endDate: endDates }});
  };

  const renderList = () => {
    return <table id="example1" className="table table-bordered">
      <thead>
        <tr>
          <th>Trainer</th>
          <th>Course</th>
          <th>Sales</th>
        </tr>
      </thead>
      <tbody>
        {list.data.filter(filterUser).map(record => renderRow(record))}
      </tbody>
    </table>
  };

  const filterUser = (listdata) => {
    return listdata.trainer.toLowerCase().includes(searchtrainer.toLowerCase()) && listdata.name.toLowerCase().includes(searchcourse.toLowerCase());
  }

  return (<div>
    <div className="content-wrapper">
      <div className="content-header">
        <div className="container-fluid">
          <div className="row mb-2">
            <div className="col-sm-6">
              <h1 className="m-0 text-dark">Trainer Sales</h1>
            </div>
            <div className="col-sm-6">
              <ol className="breadcrumb float-sm-right">
                <li className="breadcrumb-item"><a href="/">Home</a></li>
                <li className="breadcrumb-item active">Trainer Sales</li>
              </ol>
            </div>
          </div>
        </div>
      </div>
      <section className="content">
        <div className="container-fluid">
          <div className="row">
            <section className="col-lg-12 connectedSortable">
              <div className="card">
                <div className="card-body">
                  {list.loading && <div className="text-center p-3">
                    Loading data.. <div className="spinner-grow text-danger"></div>
                  </div>}                  
                  {!list.loading && <>
                    <div className="col-md-12">
                      <div className="col-md-6" style={{ float: "left" }}>
                        <div className="input-group input-group-sm">
                          <div className="input-group-prepend">
                            <span className="input-group-text">@</span>
                          </div>
                          <input type="text" className="form-control" value={searchtrainer} onChange={handleSearchTrainer} placeholder='Enter name' />
                        </div>
                      </div>
                      <div className="col-md-6" style={{ float: "left" }}>
                        <div className="card-tools">
                          <div className="input-group input-group-sm">
                            <input type="text" name="table_search" value={searchcourse} onChange={handleSearchCourse} className="form-control float-right" placeholder="Enter Course Name" />
                            <div className="input-group-append">
                              <button type="submit" className="btn btn-default">
                                <i className="fas fa-search"></i>
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-12">
                      <div className="col-md-3" style={{ float: "left" }}>
                        <div className="form-group">
                          <label>Start Date:</label>
                          <div className="input-group input-group-sm">
                            <div className="input-group-prepend">
                              <span className="input-group-text"><i className="far fa-calendar-alt"></i></span>
                            </div>
                            <input type="date" className="form-control" data-inputmask-alias="datetime" data-inputmask-inputformat="dd/mm/yyyy" data-mask="" inputmode="numeric" name="startDate" onChange={handleStartDate} value={startDate} />
                          </div>
                        </div>
                      </div>
                      <div className="col-md-3" style={{ float: "left" }}>
                        <div className="form-group">
                          <label>End Date</label>
                          <div className="input-group input-group-sm">
                            <div className="input-group-prepend">
                              <span className="input-group-text"><i className="far fa-calendar-alt"></i></span>
                            </div>
                            <input type="date" className="form-control" data-inputmask-alias="datetime" data-inputmask-inputformat="dd/mm/yyyy" data-mask="" inputmode="numeric" name="endDate" onChange={handleEndDate} value={endDate}/>
                          </div>

                        </div>
                      </div>
                    </div>{list.data.length === 0 && <div className="text-center bg-light p-3">No Record found!</div>}
                    {list.data.length > 0 && renderList()}
                    {list.data.length > 0 && Utils.showPagination({ ...list.pageInfo, ..._.pick(filters, ['start', 'limit']) }, gotoPage)}

                  </>}
                </div>
              </div>
            </section>
          </div>
        </div>
      </section>
    </div>
  </div>)
};

export default Trainersales;