import React, { useState, useEffect } from 'react';
import Utils from './../Utils';
import axios from 'axios';
import _, { filter } from 'lodash';

const Blogs = (props) => {

  const [list, setList] = useState({ loading: false, error: false, pageInfo: {}, data: [] });
  const [filters, setFilters] = useState({ where: {active: 1},limit: 15, start: 0 });

  const deleteRecord = (id) => (e) => {
    if (window.confirm("You are going to delete record, are you sure?")) {
      axios.delete(Utils.apiUrl(`blog/${id}`), Utils.apiHeaders())
        .then(res => {
          fetchList();
          window.alert(res.data.message);
        })
    }
  };

  const fetchList = () => {
    setList({ ...list, loading: true });
    let params = `?limit=${filters.limit}&start=${filters.start}&`;
    //params += _.map(filters.where, (v,k) => `where[${k}]=${v}`).join('&');
    axios.get(Utils.apiUrl('blog/list' + params), Utils.apiHeaders())
      .then(res => {
        if (res.data.success) {
          setList({ ...list, loading: false, error: false, pageInfo: res.data.pageInfo, data: res.data.data });
        } else {
          setList({ ...list, loading: false, error: res.data.message, pageInfo: {}, data: [] });
        }
      })
  };

  const toggleActive = (id) => (e) => {
    if (window.confirm(`You are going to ${e.target.checked ? "activate" : "de-activate"} this blog, are you sure?`)) {
      axios.put(Utils.apiUrl(`blog/edit/${id}`), `active=${e.target.checked ? 1 : 0}`, Utils.apiHeaders())
        .then(res => {
          window.alert(res.data.message);
          fetchList();
        })
    }
  };

  useEffect(fetchList, [filters]);

  const gotoPage = (page) => (e) => {
    const start = (page - 1) * filters.limit;
    setFilters({ ...filters, start: start });
  };

  const renderList = () => {
    return <table id="example1" className="table table-bordered table-striped">
      <thead>
        <tr>
          <th>#ID</th>
          <th>Name</th>
          <th>Slug</th>
          <th>Preview</th>
          <th>Action</th>
        </tr>
      </thead>
      <tbody>
        {list.data.map(record => <tr key={record.id}>
          <td>{record.id}</td>
          <td>{record.name}</td>
          <td>{record.slug}</td>
          <td className="bg-dark text-center">
            <a href={`${process.env.REACT_APP_SITE_URL}/trainers/${_.get(record, 'slug', '')}/blogs/${_.get(record, 'slug', '')}`} title={_.get(record, 'name', '')} target="_blank"><i className="fas fa-search"></i></a>
          </td>
          <td>
            <button className="btn btn-danger btn-sm" onClick={deleteRecord(record.id)}>
              <i className="fas fa-trash"></i>
            </button>
            <div className="custom-control custom-switch">
              <input type="checkbox" className="custom-control-input" id={`active${record.id}`} name="active" value="1" defaultChecked={record.active} onChange={toggleActive(record.id)} />
              <label className="custom-control-label" htmlFor={`active${record.id}`}></label>
            </div>
          </td>
        </tr>)}

      </tbody>
    </table>
  };

  return (<div>
    <div className="content-wrapper">
      <div className="content-header">
        <div className="container-fluid">
          <div className="row mb-2">
            <div className="col-sm-6">
              <h1 className="m-0 text-dark">Blog Management</h1>
            </div>
            <div className="col-sm-6">
              <ol className="breadcrumb float-sm-right">
                <li className="breadcrumb-item"><a href="/">Home</a></li>
                <li className="breadcrumb-item active">Blog Management</li>
              </ol>
            </div>
          </div>
        </div>
      </div>

      <section className="content">
        <div className="container-fluid">
          <div className="row">
            <section className="col-lg-12 connectedSortable">
              <div className="card">
                <div className="card-header">
                  <h3 className="card-title">Blog List #</h3>
                </div>
                <div className="card-body">
                  {list.loading && <div className="text-center p-3">
                    Loading data.. <div className="spinner-grow text-danger"></div>
                  </div>}
                  {!list.loading && <>
                    {list.data.length === 0 && <div className="text-center bg-light p-3">No Record found!</div>}
                    {list.data.length > 0 && renderList()}
                    {list.data.length > 0 && Utils.showPagination({ ...list.pageInfo, ..._.pick(filters, ['start', 'limit']) }, gotoPage)}
                  </>}
                </div>
              </div>
            </section>
          </div>
        </div>
      </section>
    </div>
  </div>)
};

export default Blogs;