import {LoginLayout,DefaultLayout} from './layouts';

import {Login,Dashboard,Role,Admin, Users, Sociallink, Blogs, Staticpages, Pagenew, Sales,Trainersales,ContactList, TrainerCourses, TrainerBlogs} from './pages';

import Utils from './Utils';

const CustomRoutes = [  
  
  {
    'path'      : '/login', 
    'secure'    : false,    
    'exact'     : true,
    'layout'    : LoginLayout, 
    'component' : Login
  },
  {
    'path'      : '/roles',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Role,
    'accessible': Utils.canAccess('roles')
  },
  {
    'path'      : '/sociallinks',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Sociallink,
    'accessible': Utils.canAccess('roles')
  },
  {
    'path'      : '/blogs',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Blogs,
    'accessible': Utils.canAccess('blogs')
  },
  {
    'path'      : '/static-pages',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Staticpages,
    'accessible': Utils.canAccess('static-pages')
  },
  {
    'path'      : '/page-new',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Pagenew,
    'accessible': Utils.canAccess('page-new')
  },
  {
    'path'      : '/sales',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Sales,
    'accessible': Utils.canAccess('sales')
  },
  {
    'path'      : '/trainer-sales',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Trainersales,
    'accessible': Utils.canAccess('trainer-sales')
  },
  {
    'path'      : '/admins',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Admin,
    'accessible': Utils.canAccess('admin')
  },
  {
    'path'      : '/users',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Users,
    'accessible': Utils.canAccess('users')
  },
  {
    'path'      : '/contact',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : ContactList
  },
  {
    'path'      : '/courses',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : TrainerCourses
  },
  {
    'path'      : '/blogs',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : TrainerBlogs
  },
  {
    'path'      : '/',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Dashboard
  },
  {
    'path'      : '*',
    'secure'    : true,
    'exact'     : true,
    'layout'    : DefaultLayout,
    'component' : Dashboard
  }
];

export default CustomRoutes;